@tailwind base;
@tailwind components;
@tailwind utilities;


/* tailwind css */
@layer base{
    .nav-link{
        @apply w-full text-center py-[5px] hover:bg-[#ccc] duration-500
    }
    .section-spaces{
        @apply px-[30px] py-[70px]
    }
}

/* css */
html{
    scroll-behavior: smooth;
}
.hero{
    background-image: url("../public/images/landing.jpg");
    background-size: cover;
    height: calc(100vh - 80px);
}